export default class times {
  static getTime () {
    let second = 0
    let time = []
    for (let i = 1; i <= 48; i++) {
      let line = {}
      line.id = i + ''
      let start = ''
      let end = ''
      if (second === 0) {
        start = '00:00'
      } else {
        start = this.getString(second)
      }
      second = second + 60 * 30
      if (i === 48) {
        end = '00:00'
      } else {
        end = this.getString(second)
      }
      line.label = start + '~' + end
      time.push(line)
    }
    return time
  }

  static getString (second) {
    let hour = parseInt(second / (3600)) + ''
    hour = (hour.length === 1 ? '0' : '') + hour
    console.log(hour)
    let m = (parseInt((parseFloat(second / 3600.0) - parseInt(second / 3600.0)) * 60)) + ''
    m = m + (m.length === 1 ? '0' : '')
    return hour + ':' + m
  }
}
