<template>

  <div>
    <h3>场馆信息编辑</h3>
    <Divider orientation="left">信息填写</Divider>
    <Row>
      <Col :xs="24" :sm="24" :md="12" :lg="12">
        <Form :model="formData" :label-width="100">
          <FormItem label="场馆编号">
            <Input v-model="formData.venue_no" placeholder="请填写场馆编号"></Input>
          </FormItem>
          <FormItem label="场馆名称">
            <Input v-model="formData.name" placeholder="请填写场馆名称"></Input>
          </FormItem>
          <FormItem label="所属空间">
            <Select v-model="formData.space_id">
              <Option v-for="(item,index) in space" v-bind:key="index" :label="item.name"
                      :value="item.id"></Option>
            </Select>
          </FormItem>
          <FormItem label="备注">
            <Input v-model="formData.remark" placeholder="请填写备注"></Input>
          </FormItem>
          <FormItem label="禁用时间段">
            <Select multiple v-model="formData.disable_time">
              <Option v-for="(item,index) in times" v-bind:key="index" :label="item.label"
                      :value="item.id" ></Option>
            </Select>
          </FormItem>

          <FormItem label="状态">
            <Select v-model="formData.status">
              <Option label="锁定" :value="0"></Option>
              <Option label="可用" :value="1"></Option>
            </Select>
          </FormItem>

          <FormItem label="价格">
            <InputNumber
              style="width: 100%"
              v-model="formData.accommodate_num">
            </InputNumber>
          </FormItem>

          <FormItem>
            <Button type="success" long @click="submit">提交</Button>
          </FormItem>

        </Form>
        <Spin size="large" fix v-if="load"></Spin>
      </Col>

    </Row>
  </div>
</template>

<script>
import api from './api'
import times from '../../js/utils/times'
import spaceApi from '../space/api'

export default {
  name: 'edit',
  data () {
    return {
      formData: {
      },
      space: [],
      load: false,
      times: []
    }
  },
  mounted () {
    if (this.$route.query.id != null) {
      this.loadData()
    }
    this.loadSpace()
    this.initTimes()
  },
  methods: {
    initTimes () {
      this.times = times.getTime()
    },
    loadSpace () {
      spaceApi.select(res => {
        this.space = res.data.data
      })
    },
    loadData () {
      this.load = true
      api.detail({ id: this.$route.query.id }, res => {
        this.load = false
        this.formData = res.data.data
        this.formData.space_id = this.formData.space.id
      }, () => {
        this.load = false
      })
    },
    submit () {
      this.load = true
      // this.formData.disable_time ='1212'
      api.save(this.formData, () => {
        this.load = false
        this.$router.push({ name: 'venue' })
      }, () => {
        this.load = false
      })
    }
  }
}
</script>

